*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

p{
  font-size:1.6rem;
}

h1{
  font-size: 6rem;
}

.App{
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0,0,0, 0.4);
  color: #fff;

}

.container{
  max-width: 700px;  
  height: 700px;
  margin: auto;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between; 
}

input{
  background: none;
  border: none;
  border-bottom: 1px #ccc solid;
  padding: 0 1em 0.5em 0;
  width: 80%;
  font-size: 1.1em;
  text-align: center;
  padding: 1rem;
  color: #fff;
}

.search:focus{
  outline: none;
}

.search::placeholder{
  color: #f8f8f8;
}

.App::before{
  content: '';
  background:url('https://images.unsplash.com/photo-1500835176302-48dbd01f6437?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1099&q=80')no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.App .top{
  width: 100%;
  margin: 1rem auto;
}

.App .description{
  position: relative;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(269deg);
}

.App .bottom{
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255,255,255, 0.2);
}